/**
 * 公共函数模块
 * @author: liaohuan
*/

import aes from "@/utils/aes"
import cookie from "@/utils/cookies"

let passcodeList = require("@/utils/aes.json")
let encryptKey = "login_key"

export default {
  
  /**
   * 检验通行码
  */
  validateCode (stringCode) {
    let encryptCode = aes.encrypt(stringCode)
    let result = {
      "message": "",
      "result": false
    }

    const veridateCode = passcodeList.filter((item) => {
      if (item.aesPasscode === encryptCode) {
        // 过期天数为 0 时, 表示永久
        if (item.expire === 0) {
          result.result = true
          return item
        } else {
          const activateTime = new Date(item.activate).getTime()
          const nowTime = new Date().getTime()
          // 相差的秒数
          const totalSeconds = (nowTime - activateTime) / 1000
          // 相差的天数
          const totalDays = parseInt(totalSeconds / 3600 / 24)
          if (totalDays < item.expire) {
            result.result = true
            return item
          } else {
            result.message = "已过期!"
            cookie.removeCookie(encryptKey)
          }
        }
      }
    })
    if (veridateCode.length === 0) {
      result.message = result.message ? result.message : "无效通行码!"
    } else {
      // 一次登录有效时间 3 小时
      cookie.setCookie(encryptKey, encryptCode, .125)
      result.result = true
    }

    return result
  },


  /**
   * 检查通行码是否过期
  */
  codeIsExpire () {
    const localValue = cookie.getCookie(encryptKey)
    const verifyCodeList = passcodeList.filter(item => {
      const totalSeconds = (new Date().getTime() - new Date(item.activate).getTime()) / 1000
      const totalDays = parseInt(totalSeconds / 3600 / 24)

      if (item.aesPasscode === localValue && totalDays < item.expire) {
        return item
      }
    })
    return verifyCodeList.length > 0 ? false : true
  },


  /**
   * 检查访问设备是否为手机
  */
  isMobile() {
    let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
    return flag
  }

}



