import Vue from 'vue'
import Router from 'vue-router'

import utils from "@/utils/common"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import("@/components/displayIndex"),
      beforeEnter (to, from, next) {
        // 检查是否过期
        if (!utils.codeIsExpire()) {
          next()
        } else {

          // 由于 giuhub pages 不支持 router 跳转, 需要进行判断
          if (process.env.NODE_ENV === "development") {
            alert("未授权登陆!")
            next('/login')
          } else {
            next()
          }
          
        }
      }
    },
    {
      path: '/login',
      name: "login",
      component: () => import("@/components/login")
    }
  ]
})




