<template>
  <div class="moblie_body">
    <div class="dialog_content">
      <img src="@/assets/mobile.png" class="mobile_img" alt="mobile.png" srcset="">
      <p>为了更好的体验，请将设备旋转</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "MobileDialog"
}
</script>

<style>
.moblie_body {
  position: fixed;
  width: 100%;
  height: 100%;
  background: black;
  z-index: 1;
}

.dialog_content {
  width: 100%;
  height: 100px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
  text-align: center;
  color: white;
  font-size: 14px;
}

.mobile_img {
  width: 90px;
}
</style>



