<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import MobileDialog from "@/components/mobileDialog"
import utils from "@/utils/common"
export default {
  name: 'App',
  components: {
    "mobile-dialog": MobileDialog
  },
  data () {
    return {
      isMobileDevice: false,
      isDisplayOnMobile: false
    }
  },
  watch: {
    isMobileDevice: function (val) {
      this.isMobileDevice = val
    }
  },
  created () {
    this.checkDevice()
    this.orientationChange()
  },
  mounted () {
    if (this.isMobileDevice) {
      addEventListener("onorientationchange" in window ? "orientationchange" : "resize", this.orientationChange)
    }
  },
  methods: {

    checkDevice () {
      this.isMobileDevice = utils.isMobile()
    },

    orientationChange () {
      if (window.orientation === 90 || window.orientation === -90) {
        this.isDisplayOnMobile = true
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



