import CryptoJS from 'crypto-js'

// AES-128-CBC偏移量
// 生成偏移量代码:
// parseInt(String(Math.random()).substring(2).slice(0, 16)).toString(16).toUpperCase()
const CBCIV = "1D6450813DF9BA"

export default {
    //加密
    encrypt(data){
        data = CryptoJS.MD5(data).toString()
        let key = CryptoJS.enc.Utf8.parse(CBCIV)
        let secretData = CryptoJS.enc.Utf8.parse(data)
        let encrypted = CryptoJS.AES.encrypt(
            secretData,
            key,
            {
                iv: CryptoJS.enc.Utf8.parse(CBCIV),
                mode:CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7
            }
        )
        return encrypted.toString()
    },

    //解密 (该函数暂时不能使用!!!)
    decrypt(data){
        let key = CryptoJS.enc.Utf8.parse(CBCIV)
        let decrypt = CryptoJS.AES.decrypt(
            data,
            key,
            {
                iv: CryptoJS.enc.Utf8.parse(CBCIV),
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7
            })
        return CryptoJS.enc.Utf8.stringify(decrypt).toString()
    }
}


